*{
    color: white;
}

.nombre_testimonio{

    font-size: 1.5rem;
}

.contenedor_testimonio{
    margin-top: 1.5rem;
    margin-bottom: 1rem;
    /* min-width: 100%; */
    min-width: 73.125;
    min-height: 23rem;
    display: flex;
    align-items: center;
    text-align: center;
    background-color: #0a0a23;
    border-radius: .5rem;
    -webkit-border-radius: .5rem;
    -moz-border-radius: .5rem;
    -ms-border-radius: .5rem;
    -o-border-radius: .5rem;
    box-shadow: 0 0 .5rem black;
}

.contenedor_textoTestimonio,
.parrafo_color{
    background-color: #0a0a23;
    margin: .7rem;
}

.nombre_testimonio{
    font-weight: 900;
    font-size: 1.7rem;
    margin-top: -2rem;
    margin-bottom: 2rem ;
    
}
.cargo_testimonio{
    font-weight: 700;
    font-size: 1.3rem;
    margin-top: -1rem;
    margin-bottom: 2rem;
}
.texto_testimonio{
    font-weight: 100;
    font-size: 1.1rem;
    text-align: justify;
}
.imagen_testimonio{
    max-width: 17rem;
    margin: .7rem;
    border-radius: .5rem;
    -webkit-border-radius: .5rem;
    -moz-border-radius: .5rem;
    -ms-border-radius: .5rem;
    -o-border-radius: .5rem;
}

@media screen and (max-width:720px) {

    .contenedor_testimonio{

        margin: .7rem;
        width:97% ;
        flex-direction: column;
    }
    
    .imagen_testimonio{
        width: 100%;
    }

    .nombre_testimonio{
       margin-top: 0;
    }
    .cargo_testimonio{
        margin-top: -1rem;
    }
}

